import '../css/todo.css';
import '../css/checkBox.css';

import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

export default function TodoNew(props) {
  const [task, setTask] = useState('');
  const [tasks, setTasks] = useState([]);
  // console.log(task);
  // console.log(tasks);

  //   ,
  useEffect(() => {
    if (localStorage.getItem('localTasks')) {
      const storedList = JSON.parse(localStorage.getItem('localTasks'));
      setTasks(storedList);
    }
    // console.log(props.isLogin);
  }, []);

  /**   */
  const ErrMsg = Msg => {
    alert(Msg);
    console.log(Msg);
    return;
  };

  /**   */
  const addTask = event => {
    if (props.isLogin === 'false') {
      ErrMsg(`  !`);
      return;
    }
    if (task) {
      const newTask = { 
        id: new Date().getTime().toString(),
        title: task
      };
      // console.log(newTask);
      setTasks([...tasks, newTask]);
      localStorage.setItem('localTasks', JSON.stringify([...tasks, newTask]));
      setTask('');
    }
    else ErrMsg(` !`);
  };

  /**  */
  const DeleteContent = task => {
    const deleted = tasks.filter(t => t.id !== task.id);
    setTasks(deleted);
    localStorage.setItem('localTasks', JSON.stringify(deleted));
  };

  /**   */
  const clearContent = () => {
    setTasks([]);
    localStorage.removeItem('localTasks');
  };

  //    
  /**     */
  const WarningStyle = styled.div`
    span {
      font-size: 35pt;
      color: red;
    }
  `;

  //     
  /** badgeInf
   * /     / 
   */
  let badgeInfo = null;
  if (props.isLogin === 'false') {
    badgeInfo = (
      //   
      <WarningStyle>
        <div className="badge">
          <span className="">  !</span>
        </div>
      </WarningStyle>
    );
  } 
  else {
    badgeInfo = (
      <div className="badge">
           :
        <span>
          {/*        */}
          {!tasks.length
            ? '  '
            : tasks.length === 1
            ? ' 1 '
            : tasks.length > 1
            ? ` ${tasks.length} `
            : null}
        </span>
      </div>
    );
  }
  // console.log(tasks);
  // Output area
  return (
    <div className="">
      <h1 className="title">TODO List</h1>
      {badgeInfo}
      <div className="input-area ">
        <input className="input-box" name="task" type="text" value={task} placeholder="Write your task..."
          onChange={event => setTask(event.target.value)}
        />
        <button className="input-addBtn" onClick={addTask}></button>
      </div>
      {/*     */}
      {props.isLogin === 'false' ? (
        //  element 
        <div></div>
      ) : (
        <>
          {tasks.length === 0 ? (
            <div className="info">  !</div>
          ) : (
            tasks.map(task => (
              <React.Fragment key={task.id}>
                <div className="outer">
                  <div className="container-todo">
                    <div className="grid">
                      <label className="checkbox bounce">
                        <input type="checkbox"
                          onClick={() => {
                            DeleteContent(task);
                          }}
                        />
                        <svg viewBox="0 0 21 21">
                          <polyline points="5 10.75 8.5 14.25 16 6"></polyline>
                        </svg>
                      </label>
                    </div>
                    <span className="innertext fromLeft">{task.title}</span>
                  </div>
                </div>
              </React.Fragment>
            ))
          )}
        </>
      )}
      {/**   
       *   false ,   
       *   true ,  
       */}
      {props.isLogin === 'false' || !tasks.length ? null : (
        <div className="item-bottom">
          <button className="clearBtn" onClick={() => clearContent()}>Clear</button>
        </div>
      )}
    </div>
  );
}
